


.pic-card-container {
    width: 100%;
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-end;
}

.pic-item {
    padding-top: 112px;
    width: 420px;
    height: 600px;
}

.about-me-main-container {
    width: 100%;
    display: flex;
    flex-flow: column nowrap;
    justify-content: flex-start;
}

.about-me-title {
    padding: 32px;
    padding-top: 112px;
    align-self: flex-start;
}

.about-me-intro {
    padding: 16px 32px;
}

.about-me-info {
    font-size: 32px;
    font-weight: 600;
    padding-bottom: 8px;
}

.about-me-button-container {
    margin-left: -32px;
    margin-top: 8px;
}

@media screen and (min-height: 860px) {
    .pic-item {
        padding-top: 170px;
    }

    .about-me-title {
        padding-top: 170px;
    }

}

@media screen and (max-width: 1500px) {
    .about-me-info {
        padding-bottom: 8px;
    }
}

@media screen and (max-width: 1370px) {
    .about-me-title {
        padding-bottom: 16px;
    }  

    .about-me-intro {
        padding: 0px 32px;
    } 
}

.about-me-small-container {
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    padding-top: 112px;
}

.pic-item-small {
    width: 200px;
    height: 200px;
}

.about-me-title-small {
    padding-top: 32px;
}

.about-me-intro-small {
    padding: 32px 0;
    text-align: left;
}